import React from "react"
import clamp from 'lodash-es/clamp'
import {useSpring, animated} from 'react-spring'
import { useGesture, withGesture } from 'react-with-gesture'
import { Spring } from "react-spring/renderprops";
import "./Slider.css"


function Slider({
    index,
    offsetRadius,
    gameName,
    content,
    icon,
    link,
    moveSlide,
    delta,
    down,
    animationConfig,
}){   
    const offsetFromMiddle = index - offsetRadius;
    const totalPresentables = 2 * offsetRadius + 1;
    const distanceFactor = 1 - Math.abs(offsetFromMiddle / (offsetRadius + 1));

    const translateYoffset =
    50 * (Math.abs(offsetFromMiddle) / (offsetRadius + 1));
    let translateY = 0;

    if(offsetFromMiddle === 0 && down)
    {
        translateY += delta[1] / (offsetRadius + 1);
        console.log(translateY);

        if(translateY < -63 )
        {
            moveSlide(1);
        }
        if(translateY > 63)
        {
            moveSlide(-1);
        }
            
      }

    if (offsetFromMiddle > 0) {
        translateY -= translateYoffset;
    } else if (offsetFromMiddle < 0) {
        translateY += translateYoffset;
    }

    return(
        
        <Spring
            to={{
                transform:`translate(-50%,  ${-50 + translateY}%) scale(${distanceFactor})`,
                top: `${offsetRadius === 0 ? 50 : 50 + (offsetFromMiddle * 50) / offsetRadius
                    }%`,
                opacity: distanceFactor * distanceFactor,
                zIndex: Math.abs(Math.abs(offsetFromMiddle) - 2)
            }}
            config={animationConfig}
        >

            {(style) => (
                <animated.div className="SliderContainer" 
                    style={{...style, zIndex: Math.abs(Math.abs(offsetFromMiddle) - 2)}}>
                    <div className="SliderCard" style={{}}>

                        <a className="gameName">{gameName}</a>
                        <a className="context">{content}</a>
                        <a className="icon" href={link}>
                            <img className="img" src={icon} ></img>
                        </a>
                    </div>
                </animated.div>
            )}
        </Spring>
    );
}


export default withGesture()(Slider);