import React, { Component, useState} from "react";
import './Main.css'
import VerticalCarousel from "./VerticalCarousel";
import { config } from "react-spring";
import Topnav from "../Topnav/Topnav";
import Poll from "./Poll";
import { useSpring, animated as a } from 'react-spring'

let slides = [
    {
        key: 0,
        gameName: 'Test',
        content: '유신이바보',
        icon: "logo512.png",
        link: 'aa'  
    },
    {
        key: 1,
        gameName: 'Test1',
        content: '유신이d보',
        icon: "logo512.png",
        link: 'aa'  
    },
    {
        key: 2,
        gameName: 'Test2',
        content: '유신이d보',
        icon: "logo512.png",
        link: 'aa'  
    },
    {
        key: 3,
        gameName: 'Test3',
        content: '유신이d보',
        icon: "logo512.png",
        link: 'aa'  
    }
]  


export default class Main extends Component {

    constructor(props){
        super(props);
        this.state = { 
            config : config.gentle,
            flipped: true
        } 
    }

    movePage = () =>{
        console.log(this.state.flipped);

        if(this.state.flipped)
        {
            document.getElementById('context_1').style.display = 'none';
            document.getElementById('context_2').style.display = 'flex';
        }
        else
        {
            document.getElementById('context_1').style.display = 'flex';
            document.getElementById('context_2').style.display = 'none';
        }




        document.getElementById('topnav_Button').textContent = this.state.flipped ? '게임 정보 보기' : '투표하러 가기'
        this.setState({
            flipped: !this.state.flipped
        });
    }

    render()
    {
        
        setTimeout(() => {
            document.getElementById('topnav_Button').onclick = this.movePage;
        }, 500); 

        return ( 
            <div style={{background:'black', height:'100vh'}}>
                <Topnav>
                </Topnav>

                <img className="LeftDeco" src="image/LeftDeco.svg"></img>

                <div id="main" className="main" >
                    <div id="context_1" className="GameInfo">
                        <VerticalCarousel
                            slides={slides}
                            animationConfig={config}
                        />
                    </div>
                    <div id="context_2" className="Vote">
                        <Poll>

                        </Poll>
                    </div>
                </div>

                <img className="RightDeco" src="image/RightDeco.svg"></img>


            </div>

        );

    }
  }