import React, { Componnent, useState, useEffect } from 'react';
import "./Topnav.css";

const Topnav = () => {
  const [modal, setModal] = useState();
  const [className, setClassName] = useState("layer_popup");

  const modalUp = () => {
    setModal(true);
  }
  const modalOff = () => {
    setModal(false);
  }
  useEffect(() => {
    if (modal)
      setClassName(className + ' on');
    else if (modal == false)
      setClassName('layer_popup');
  }, [modal])



  return (
    <nav className="topnav_background" >
      <div className="topnav">
        <a className="leftAtt" href="https://thecgawards.kro.kr">
          <img className="Title" src="image/Title.png"></img></a>

        <a id="topnav_Button" className="Button">투표하러 가기</a>
      
        <ul className="topnav_icon">
          <li><a href="https://www.youtube.com/channel/UCCM8oRwSn5KdywxiuLp-bfA" target="_blank">
            <img src="image/YoutubeIcon.png"></img></a></li>
          <li><a href="https://www.instagram.com/kah.cg/" target="_blank">
            <img src="image/InstaIcon.png"></img></a></li>
          <li><a href="https://www.instagram.com/kah.cg/" target="_blank">
            <img src="image/FacebookIcon.png"></img></a></li>
        </ul>

        <ul className="topnav_icon_small">
          <li onClick={modalUp}><a>
            <img src="menu.svg"></img></a></li>
        </ul>

      </div>
      <div className={className}>
        <div className="layer">
          <div className="text_area">
            <div className="title">메인</div>
            <div className="maker">
              <div className="human">일호</div>
              <div className="human">이호</div>
              <div className="human">삼호</div>
              <div className="human">사호</div>
              <div className="human">사호</div>
            </div>
          </div>
          <div className="btn_area">
          </div>
          <button className="btn_close" onClick={modalOff}>CLOSE</button>
        </div>
        <div className="dimmed">
        </div>
      </div>
    </nav>

  );
}




export default Topnav;
