import React from 'react';
import PropTypes from "prop-types";
import Slider from "./Silder"
import DotNav from "./DotNav"

function mod(a, b) {
    return ((a % b) + b) % b;
  }

class VerticalCarousel extends React.Component{
    state = {
        index: 0
        
    };

    componentDidMount = () => {
        document.addEventListener("keydown", event => {
          if (event.isComposing || event.keyCode === 229) {
            return;
          }
          if (event.keyCode === 38) {
            this.moveSlide(-1);
          }
          if (event.keyCode === 40) {
            this.moveSlide(1);
          }
        });
      };

    static defaultProps = {
        offsetRadius: 1,
        animationConfig: { tension: 120, friction: 14, duration: 100 }
    };
    
    static propTypes = {
        slides: PropTypes.arrayOf(
            PropTypes.shape({
                key: PropTypes.any,
                gameName: PropTypes.string,
                content: PropTypes.string,
                icon: PropTypes.string,
                link: PropTypes.string
            })
        ).isRequired,
    };

    modBySlidesLength = (index) => {
        return mod(index, this.props.slides.length);
      };

    clampOffsetRadius(offsetRadius) {
        const { slides } = this.props;
        const upperBound = Math.floor((slides.length - 1) / 2);
    
        if (offsetRadius < 0) {
          return 0;
        }
        if (offsetRadius > upperBound) {
          return upperBound;
        }
    
        return offsetRadius;
    }

    moveSlide = (direction) => {
        this.setState({
          index: this.modBySlidesLength(this.state.index + direction),
          goToSlide: null
        });
    };

    moveIndex = (number) => {
      this.setState({
        index: number
      });
    }

    getSlidesCount()
    {
      const {slides} = this.props;
      const presentableSlides = slides;

      return presentableSlides.length;
    }

    getPresentableSlides() {
        const { slides } = this.props;
        const { index } = this.state;
        let { offsetRadius } = this.props;
        offsetRadius = this.clampOffsetRadius(offsetRadius);
        const presentableSlides = new Array();

        for (let i = -offsetRadius; i < 1 + offsetRadius; i++) {

            presentableSlides.push(slides[this.modBySlidesLength(index + i)]);
        }

        return presentableSlides;
      }


    render(){
        const {animationConfig, offsetRadius} = this.props;

        return(
            <React.Fragment>
              <div>
                {this.getPresentableSlides().map((slider, presentIndex) => (
                    <Slider 
                        index={presentIndex}
                        offsetRadius={this.clampOffsetRadius(offsetRadius)}
                        key={slider.key} 
                        gameName={slider.gameName}
                        content={slider.content} 
                        icon={slider.icon} 
                        link={slider.link}
                        moveSlide={this.moveSlide}
                        animationConfig={animationConfig}
                        >

                    </Slider>
                ))}
                <DotNav
                  Count={this.getSlidesCount()}
                  index={this.state.index}
                  moveIndex={this.moveIndex}
                  >
                    
                  </DotNav>
              </div>
            </React.Fragment>
        )
    }
}

export default VerticalCarousel;