import React, { Component, useState, useEffect } from 'react';
import "./Poll.css"
import {v4 as uuid} from 'uuid'
import {useCookies} from 'react-cookie';
import PollContent from './PollContent';


let contents = [
    {
        content:[
            {
                name:'HelpBee',
                index: 0,
                image: "logo512.png"
            },
            {
                name:'StockMarket',
                index: 1,
                image: "logo512.png"
            },
            {
                name: "Arksung's Epidemic",
                index: 2,
                image: "logo512.png"
            },
            {
                name: "Arksung's Epidemic",
                index: 2,
                image: "logo512.png"
            }
        ]
    },
    {
        content:[
            {
                name:'HelpBee',
                index: 0,
                image: "logo512.png"
            },
            {
                name:'StockMarket',
                index: 1,
                image: "logo512.png"
            },
            {
                name: "Arksung's Epidemic",
                index: 2,
                image: "logo512.png"
            }
        ]
    },
    {

    }
]




function Poll({
    content
})
{

    const [id, setID] = useState("");

    const [cookies, setCookie, removeCookie] = useCookies(['uniqueId']);

    useEffect(()=>{
        const SOURCE = 'https://spreadsheets.google.com/feeds/list/1rl0fCsvCnAg9uWTKzztFlFd3Q2E08-zjRAeCGCd-55s/1/public/full?alt=json'

        fetch(SOURCE)
        .then(res => res.json())
        .then(
            (result) => {
                console.log(result['feed']['entry'][0].gsx$postid['$t']);
                content = result['feed']['entry'][0].gsx$postid['$t'][2];
            },
            // 주의: 컴포넌트의 실제 버그에서 발생하는 예외사항들을 넘기지 않도록 
            // 에러를 catch() 블록(block)에서 처리하기보다는 
            // 이 부분에서 처리하는 것이 중요합니다.
            (error) => {
                    
            }
        )

        if(cookies.uniqueId !== undefined)
        {
            setID(cookies.uniqueId);
        }
        else{
            const newId = uuid();
            setID(newId);
            setCookie('uniqueId', newId);
        }
    })

    return(
        <div className="Poll">
            
            <div className="PollContainer">
                <PollContent
                    id= {id}
                    contents={contents[0].content}
                >
                </PollContent>
            </div>

        </div>
    )
}




export default Poll;