import React, { Component, useState, useEffect } from 'react';
import './PollContent.css'
import $ from "jquery";
import {Bar, HorizontalBar} from 'react-chartjs-2'

class PollContent extends React.Component{

    constructor(props){
        super(props);
        this.state = { 
            id: '',
            contents: null,
            count: 0,
            half: 0,
            data:{
                labels:['1','2','4'],
                datasets:[
                    {
                        label: '투표현황',
                        data: [2, 5, 7]
                        
                    }
                ]
            },
            bar:null
        } 
    }

    componentDidMount = () => {
        this.setState({
            count: this.props.contents.length,
            half: this.props.count / 2,
            bar: 
            <Bar
                data={this.state.data}
                height={300}
                width={1200}
                options={
                    {
                        maintainAspectRatio: false
                    }
                }
            />
        })

        
    };

    voteGame = (id, index)=> {

        $.ajax({
            type: "GET",
            url: "https://script.google.com/macros/s/AKfycbz0-j2Y5LhXOA6ug_r4bQfoC0QE5MD3zYcsQ7iAkDLh6JXCG1yBxTYFtaiUfU3IXwHd/exec",
            data: {
              "post_ID": id,
              "choice": index
            },
            success: function(response){
                alert("투표 완료");
            }
        });
    }

   


    render()
    {
        return(
            <React.Fragment>

                <div className='Info'>
                    <a className="AwardName">~~~~~~~!!!!!!!상</a>
                    <button className='PollState'>투표 현황</button>
                </div>

                {this.props.contents.map((content, index) => (
                    <div className="PollContent" key={index} style={{display:'none'}}>
                        <div>
                            <a>{content.name}</a>
                            <img src={content.image}></img>
                            <button onClick={() => this.voteGame(this.props.id, content.index)}>선택하기</button>
                        </div>
                    </div>
                ))}
                

                <div id="VoteResult">
                    {this.state.bar}
                </div>
            </React.Fragment>
        )
    }
}

export default PollContent;